import React, { useState } from 'react';

export default function Question({ options, selected }) {
  const [answer, setAnswer] = useState(options)
  return (
    <div>
      {answer.map((text, index) => (
        <li>
          <button
            onclick={() => { 
              setAnswer([text]); selected(text);
            }}
          >
           {text}
          </button>
        </li>)
      )}
    </div>
  );
}